/* Base CSS */
@font-face {
  font-family: RusticLoveTattoo;
  src: url(../fonts/RusticLoveTattoo.otf);
}
.alignleft {
  float: left;
  margin-right: 15px;
}

.alignright {
  float: right;
  margin-left: 15px;
}

.aligncenter {
  display: block;
  margin: 0 auto 15px;
}

a:focus {
  outline: 0 solid;
}
a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

h2 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h3 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h4 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h5 {
  margin: 0 0 15px;
  color: #1B1B21;
}

h6 {
  margin: 0 0 15px;
  color: #1B1B21;
}

body {
  color: #545051;
  font-family: "Open Sans", sans-serif;
}

.phone-embed {
  overflow-x: hidden;
  position: relative;
  z-index: 1;
  width: 200px;
  height: 327px;
  margin-top: -150px;
  background: url("../img/phone.png") no-repeat;
  background-size: 100%;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 320px) {
  .phone-embed {
    width: 380px;
    height: 590px;
    margin-top: -280px;
  }
}
@media (min-width: 992px) {
  .phone-embed {
    position: absolute;
    right: 80px;
    top: 70px;
    z-index: 999;
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .phone-embed {
    right: 180px;
  }
}
.phone-embed iframe {
  position: absolute;
  border-radius: 16px;
  top: -10px;
  left: 37px;
  width: 128px;
  height: 277px;
}
@media (min-width: 320px) {
  .phone-embed iframe {
    top: 15px;
    left: 70px;
    width: 243px;
    border-radius: 28px;
    height: 525px;
  }
}

.hero {
  padding-top: 1em;
  padding-bottom: 1em;
  text-align: left;
  height: 400px;
  position: relative;
  box-shadow: 0px 3px 5px 0px #b0b0b0;
}
.hero .gabe {
  position: absolute;
  bottom: 10px;
  right: 40px;
  font-size: 14pt;
  color: #ffffff;
  z-index: 9999;
  width: 180px;
  transform: rotate(-3deg);
  font-family: "BenchNine";
}
@media (min-width: 320px) {
  .hero {
    height: 800px;
  }
}
@media (min-width: 992px) {
  .hero {
    height: auto;
  }
}
.hero .accent-star {
  top: auto;
  bottom: 120px;
  right: 25%;
}
@media (min-width: 992px) {
  .hero .accent-star {
    bottom: auto;
    top: 10%;
    right: 10%;
  }
}
.hero .logo-container {
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 992px) {
  .hero .logo-container {
    position: absolute;
    top: 20px;
    left: 40px;
  }
}
@media (max-width: 991px) {
  .hero .logo-container {
    text-align: center;
  }
}
.hero .logo-container a {
  color: #fff;
}
