  width: 100px;
}
.hero .logo-container .logo-hasbro {
  width: 90px;
}
.hero .logo-container p {
  color: #fff;
  font-size: 10pt;
  margin-top: -10px;
  text-align: center;
  font-style: italic;
  font-family: "Arial", cursive;
}
.hero .hero-container {
  max-width: none;
}
@media (max-width: 1449px) {
  .hero .hero-container {
    padding-top: 1rem;
  }
}
.hero .hero-container .content {
  position: relative;
}
@media (max-width: 991px) {
  .hero .hero-container .content {
    text-align: center;
  }
}
.hero .hero-container .heading {
  font-size: 35px;
  font-weight: 300;
  transform: rotate(-3deg);
  text-align: center;
  display: inline-block;
}
@media (max-width: 319px) {
  .hero .hero-container .heading {
    font-size: 25px;
  }
}
@media (max-width: 767px) {
  .hero .hero-container .heading {
    font-size: 25px;
    margin-top: 13px;
  }
}
@media (min-width: 992px) {
  .hero .hero-container .heading {
    margin-top: 2.5rem;
    padding-left: 2rem;
  }
}
@media (min-width: 1200px) {
  .hero .hero-container .heading {
    margin-top: 4rem;
    margin-left: 1em;
    padding-left: 0;
  }
}
.hero .hero-container .heading .big {
  font-family: "Anton";
  display: block;
  font-size: 24px;
}
@media (min-width: 320px) and (max-width: 767px) {
  .hero .hero-container .heading .big {
    font-size: 32px;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .hero .hero-container .heading .big {
    font-size: 46px;
  }
}
@media (min-width: 992px) {
  .hero .hero-container .heading .big {
    font-size: 5vw;
  }
}
@media (min-width: 1200px) {
  .hero .hero-container .heading .big {
    font-size: 6vw;
  }
}
.hero .hero-container .heading .hashtag {
  font-size: 0.7em;
  color: #efff00;
  line-height: 1em;
  vertical-align: middle;
  margin-right: 0.2em;
}
.hero .hero-container .lead {
  margin-top: 12pt;
  font-size: 15pt;
  color: #f1f1f1;
}

